.card {
   margin-right: 30px;
   margin-bottom: 30px;
   border: 1px solid #f3f3f3;
   padding: 20px;
   width: 210px;
   border-radius: 40px;
   transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

   &:hover {
      box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.08);
      transform: translateY(-5px);
   }

   .favorite {
      position: absolute;
      cursor: pointer;
   }

   .plus {
      cursor: pointer;
   }

   span {
      font-size: 13px;
      opacity: 0.5;
      text-transform: uppercase;
   }

   b {
      font-size: 13px;
   }

   h5 {
      font-weight: 400;
      font-size: 14px;
   }
}
